.header {
    padding: 30px 0;

    @include small-mobile {
        padding: 10px 0;
    }

    &__logo-svg {
        @include small-mobile {
            width: 39px;
            height: 56px;
        }
    }

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__user {
        display: flex;
        gap: 20px;
    }
}

.nav {
    display: flex;
    align-items: center;

    @include small-tablet {
        order: -1;
    }

    &__list {
        display: flex;
        align-items: center;

        @include small-tablet {
            display: none;
        }
    }

    &__list.menu--active {
        display: block;
    }

    &__item:not(:last-child) {
        margin-right: clamp(30px, 5vw, 65px);

        @include small-tablet {
            margin-right: 0;
        }
    }

    &__link {
        font-family: var(--font-Cormorant);
        color: var(--dark-color);
        font-size: 18px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

.popup-btn {
    display: none;
}

.popup-btn {
    @include small-tablet {
        display: block;
        width: 40px;
        height: 40px;

        &__line {
            width: 100%;
            height: 2px;
            background-color: var(--dark-color);
            display: block;
            margin: 5px 0;
        }
    }
}