.collection {
    &__top {
        width: 704px;
        max-width: 100%;
        margin: 0 auto;
        text-align: center;
        padding-bottom: clamp(50px, 7vw, 150px);
    }

    &__title {
        margin: 0;
        color: var(--dark-color);
        font-family: var(--font-Cormorant);
        font-size: 40px;
        font-style: italic;
        font-weight: 300;
        line-height: normal;
        @include mobile{
            font-size: 24px;
        }
        @include small-mobile {
            font-size: 16px;
        }
    }

    &__content {
        &-title {
            text-align: center;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        row-gap: 20px;
    }

    &__link {
        display: inline-block;
    }

    &__item {
        text-align: center;
        display: inline-block;
        flex: 1 208px;
    }

    &__img {
        width: 208px;
        max-width: 100%;
        height: 208px;
        max-height: 100%;
        background-color: #e6e6e6;
    }

    &__name {
        margin: 0;
        margin-top: 10px;
        color: var(--dark-color);
        font-family: var(--font-Cormorant);
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }
}

.collection__item {
    transition: box-shadow 0.3s ease;

    @include hover {
        box-shadow: var(--dark-color) 0px 0px 2px;
    }
}