.graph-modal:before {
    content: '';
    vertical-align: top;
    @include small-mobile {
        content: none;
    }
}
.graph-modal__close {
    background-image: url('../img/svg/cross.svg');
}
.graph-modal__container {
    margin: 0;
    border-radius: 0;
    padding: 0 20px;
    @include small-mobile {
        height: 100%;
        min-height: 700px;
        width: 100%;
    }
}

.graph-modal__close {
    right: 20px;
    top: 20px;
}
.graph-modal__content {
    @include small-mobile {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: inherit;
    }
}
.graph-modal__content .nav__list {
    display: flex;
    flex-direction: column;
    margin-top: 60px;

    .nav__item:not(:last-child) {
        margin-bottom: 30px;
    }

    .nav__link {
        font-size: 24px;
    }

    margin-bottom: 60px;
}

.modal__contacts {
    padding-top: 60px;
    margin-bottom: 60px;
    text-align: center;
    border-top: 1px solid var(--beige-light-color);
    &-list {
        display: flex;
        flex-direction: column;
    }

    &-item:not(:last-child) {
        margin-bottom: 35px;
    }
    &-link{
        font-size: 16px;
        color: #000;
    }
}

.modal__bottom {
    padding: 20px;
    margin: 0 -20px;
    background-color: var(--beige-light-color);
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}