.comments {
    padding-top: clamp(30px, 5vw, 60px);
    padding-bottom: clamp(30px, 5vw, 60px);
    background: var(--beige-light-color);

    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 25px;
    }

    &__item {
        flex: 1;
        min-width: 280px;
        max-width: 100%;
    }

    &__text {
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 300;
        margin: 0;
        font-style: italic;
    }
    &__author{
        margin: 10px auto 0;
    }
}