.blog {
    &__items {
        flex-wrap: wrap;
        justify-content: space-between;
        margin-bottom: clamp(30px, 5vw, 60px);
        gap: 40px 20px ;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        @include small-tablet {
            display: block;
        }
    }
    &__item:not(:last-child){
        margin-bottom: 30px;
    }
    &__item:nth-child(even){
        @media (min-width:576px) and (max-width:768px ) {
            margin-left: 100px;
        }
    }
    &__article {
        display: flex;
        min-height: 180px;
        @include small-mobile{
            flex-direction: column;
        }
    }

    &__img {
        flex: 1;

        img {
            display: block;
        }
    }

    &__content {
        flex: 1;
        margin-left: 20px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        @include small-mobile{
            margin-top: 10px;
            margin-left: 0px;
        }
        &-title {
            margin: 0;
            margin-bottom: 10px;
            color: var(--dark-color);
            font-family: var(--font-Cormorant);
            font-size: 24px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
        }
    }

    &__text {
        margin: 0;
        color: var(--dark-color);
        font-size: 13px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
    }

    &__link {
        margin-top: auto;
        display: inline-block;
        color: var(--dark-color);
        font-size: 13px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
    }

    &__span {
        padding-right: 12px;
        position: relative;
        transition: color 0.3s ease-in-out;

        &::after {
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            background-image: url('../img/svg/arrow.svg');
            background-repeat: no-repeat;
            width: 5px;
            height: 10px;
            transition: transform 0.3s ease-in-out;
        }

    }
    &__btn{
        margin: 0 auto;
        text-align: center;
    }
}

.blog__span {
    @include hover {
        color: var(--beige-color);
    }
}


.blog__span:hover:after {
    transform: translateY(-50%) translateX(5px);

}