.top {
    position: relative;
    background: url('../img/section-top/bg.jpg') no-repeat;
    background-size: cover;
    min-height: 500px;
    &::before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 40.1%, #000 97.4%);
    }
    &__container {
        position: absolute;
        bottom: 48px;
        left: 0;
        right: 0;
        margin: 0 auto;
    }

    &__title {
        margin: 0;
        margin-bottom: 20px;
        color: var(--light-color);
        font-family: var(--font-Cormorant);
        font-size: 60px;
        font-style: normal;
        font-weight: 400;
        line-height: 100%;
        width: 789px;
        max-width: 100%;
        @include mobile{
            font-size: 40px;
        }
        @include small-mobile{
            font-size: 24px;
        }
    }

    &__text {
        margin: 0;
        color: var(--light-color);
        font-family: var(--font-Cormorant);
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        width: 558px;
        max-width: 100%;
        @include small-mobile {
            font-size: 13px;
        }
    }
}