.footer {
    background-color: var(--beige-light-color);
    padding-top: clamp(50px, 5vw, 100px);

    &__list {
        padding-bottom: clamp(50px, 5vw, 100px);
    }

    &__item {
        grid-column: 3 span;
    }

    &__item:nth-of-type(2) {
        @include small-tablet {
            margin-bottom: 50px;
        }
    }

    &__item-stay-in {
        display: flex;
        grid-column: 6 span;
        gap: 50px;
        @include tablet{
            flex-direction: column;
        }
    }

    &__item-descr {
        margin: auto;
    }

    &__item-title {
        margin: 0;
        margin-bottom: clamp(25px, 5vw, 50px);
        color: var(--dark-color);
        font-family: var(--font-Cormorant);
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
    }

    &__item-inner:not(:last-child) {
        margin-bottom: 20px;
    }

    &__item-link {
        display: inline-block;
        margin: 0;
        color: var(--dark-color);
        font-size: 16px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
    }

    &__item-text {
        margin: 0;
        margin-bottom: 20px;
    }

    &__item-content {
        @include small-mobile {
            text-align: center;
        }
    }

    &__item-contacts {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        gap: 40px;
        @include mobile{
            display: flex;
            justify-content: center;
        }
    }

    &__item-phone {
        margin-bottom: 20px;
    }

    &__item-blockquote {
        color: var(--dark-color);
        font-size: 16px;
        font-style: italic;
        font-weight: 300;
        line-height: normal;
        margin-bottom: 20px;
    }

    &__item-author {
        color: var(--dark-color);
        font-size: 12px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        width: 155px;
        max-width: 100%;
    }

    &__item-phone-link,
    &__item-mail-link {
        display: inline-block;
        position: relative;
        color: var(--dark-color);
        font-size: 13px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        padding-left: 35px;

        &::before {
            content: '';
            width: 22px;
            height: 20px;
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            background-repeat: no-repeat;
        }


    }

    &__item-text {
        color: var(--dark-color);
        font-size: 13px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
    }

    &__item-phone-link {
        &::before {
            content: '';
            background-image: url('../img/section-footer/phone.png');
        }
    }

    &__item-mail-link {
        &::before {
            content: '';
            background-image: url('../img/section-footer/mail.png');
        }
    }

    &__item-socials {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        grid-column: 2;
    }

    &__bottom {
        padding-bottom: 25px;

        @include small-tablet {
            display: flex;
            justify-content: space-between;
            padding-bottom: 10px;
        }
    }

    &__copyright {
        margin: 0;
        color: var(--dark-color);
        font-size: 16px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        grid-column: 3 span;
    }
    &__payment{
        grid-column: 3 span;
        display: flex;
        align-items: center;
        gap: 20px;
    }
}