.more {
    padding-top: clamp(50px, 5vw, 100px);
    padding-bottom: clamp(50px, 5vw, 100px);
    position: relative;
    background-color: var(--beige-light-color);
    background-image: url('../img/section-more/tea.png');
    background-repeat: no-repeat;
    background-position: right center;
    @include mobile {
        background-image: none;
    }
    &__content {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 20px;

        @include tablet {
            flex-direction: column;
        }
    }

    &__item {
        flex: 1;
        width: 100%;

    }

    &__title {
        text-align: left;

        @include tablet {
            text-align: center;
        }
    }

    &__descr {
        margin: 0 auto;
        text-align: center;
        width: 780px;
        max-width: 100%;
        margin-bottom: clamp(30px, 5vw, 60px);
    }

    &__text {
        margin: 0;
        color: var(--dark-color);
        font-size: 20px;
        font-style: normal;
        font-weight: 300;
        line-height: 150%;

        @include mobile {
            font-size: 16px;
        }
    
    }

    &__form {
        margin: 0 auto;
        width: 580px;
        max-width: 100%;
    }

    .form__wrapper{
        display: flex;
        align-items: flex-end;
    }

    .form__label {
        flex: 1;
        width: 100%;
    }

    .form__input {
        width: inherit;
        background-color: transparent;
        padding-left: 15px;
        border-bottom: 1px solid #B8B58B;
        outline: 0;
        font-size: 20px;
        font-style: normal;
        font-weight: 300;
        line-height: 150%;

        &::placeholder {
            color: #B8B58B;
        }

        @include small-mobile{
            font-size: 16px;
        }
    }
    .form__input.focus-visible {
        border-bottom: 1px solid var(--beige-color);
    }

    .custom-checkbox {
        display: inline-block;
        margin-top: 15px;

        &__content {
            color: var(--dark-color);
            font-size: 13px;
            font-style: normal;
            font-weight: 300;
            line-height: normal;
        }
    }

    .btn {
        margin-left: 15px;
    }
}