html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  scroll-behavior: smooth;
  font-family: var(--font-OpenSans, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);

  @include small-tablet {
    grid-template-columns: auto;
  }
}

.title {
  color: var(--darkc-color);
  font-family: var(--font-Cormorant);
  margin: 0 auto;
  margin-bottom: clamp(30px, 5vw, 60px);
  font-size: clamp(24px, 5vw, 40px);
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: center;
}

.link {
  display: inline-block;
  position: relative;
  transition: color 0.3s ease-in-out,
    opacity 0.3s ease;

  &::after {
    content: '';
    position: absolute;
    bottom: -3px;
    left: 0;
    height: 1px;
    width: 0%;
    transition: width 0.3s ease-in-out;
    background-color: var(--dark-color);
  }

  @include hover {
    color: var(--beige-color);
  }

  &:active {
    opacity: 0.6;
  }
}

.link.focus-visible {
  color: #d89a36;

}

.link:hover::after {
  width: 100%;
}

.svg-hover {
  transition: fill 0.3s ease-in-out;

  @include hover {
    fill: var(--beige-color);
  }

  .focus-visible {
    fill: var(--beige-color);
  }
}

.section {
  padding-top: clamp(50px, 7vw, 150px);
  padding-bottom: clamp(50px, 7vw, 150px);
}

blockquote {
  margin: 0;
  display: block;
}

.btn {
  display: inline-block;
  margin: 0 auto;
  padding: 12px;
  width: 149px;
  max-width: 100%;
  border: 1px solid var(--dark-color);
  background-color: var(--light-color);
  transition: background-color 0.3s ease-in-out,
    color 0.3s ease-in-out,
    opacity 0.3s ease,
    transform 0.3s ease;

  @include hover {
    background-color: var(--dark-color);
    color: var(--light-color);
  }

  @include mobile {
    width: 116px;
  }
  &:active {
    opacity: 0.8;
    transform: scale(0.9);
  }
}

.btn.focus-visible {
  background-color: var(--dark-color);
  color: var(--light-color);
}

.footer__item-social-link.focus-visible {
  outline: 0;
}

.footer__item-img {
  transition: fill 0.3s ease-in-out;
}

.footer__item-social-link .footer__item-facebook {
  @include hover {
    fill: #3b5998;
  }
}

.footer__item-social-link.focus-visible .footer__item-facebook {
  fill: #3b5998;
}

.footer__item-social-link .footer__item-youtube {
  @include hover {
    fill: #FF0000;
  }
}

.footer__item-social-link.focus-visible .footer__item-youtube {
  fill: #FF0000;
}

.footer__item-social-link .footer__item-instagram {
  @include hover {
    fill: #833AB4;
  }
}

.footer__item-social-link.focus-visible .footer__item-instagram {
  fill: #833AB4;
}

.footer__item-social-link .footer__item-twitter {
  @include hover {
    fill: #1D9BF0;
  }
}

.footer__item-social-link.focus-visible .footer__item-twitter {
  fill: #1D9BF0;
}

.footer__item-phone-link.focus-visible {
  outline: 0;
  color: var(--beige-color);
}

.footer__item-mail-link.focus-visible {
  outline: 0;
  color: var(--beige-color);
}