.popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #0000009c;
    color: white;
    text-align: center;
    padding: 0 20px;
    display: none;
}

.popup.is-active{
    display: block;
}

.popup-content{
    position: relative;
    width: 600px;
    max-width: 100%;
    margin: 0 auto;
    background-color: var(--beige-light-color);
    color: var(--dark-color);
    border-radius: 20px;
    font-size: 24px;
    padding: 50px 20px;
    top: 50%;
    transform: translateY(-50%);
    @include mobile {
        font-size: 18px;
        padding: 30px;
    }
}