.priorities {
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 50px;
        @include small-mobile {
            gap: 40px;
        }
    }

    &__item {
        text-align: center;
        flex: 1;
        flex-basis: 257px;
        max-width: 100%;
    }

    &__item:hover svg {
        fill: #d89a36;
    }

    &__subtitle {
        margin: 0;
        color: var(--darkc-color);
        font-size: 30px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        margin-bottom: 10px;
        @include small-mobile {
            font-size: 20px;
        }
    }

    &__text {
        margin: 0;
        color: var(--darkc-color);
        font-size: 20px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
        margin-bottom: 40px;
        @include small-mobile {
            font-size: 13px;
            margin-bottom: 20px;
        }
    }

    svg {
        fill: #754E29;
    }
}